import React from "react";

const Account_Info = () => {
  return (
    <div className="Main_Cntnr">
      <div className="logobox_Settings">
        <div className="logo">
          <img
            src="https://smoothhiring.com/wp-content/uploads/2021/03/Smooth-hiring-logo-001-02.png"
            width="200"
            height="40"
            alt="logo"
          />
        </div>
        <div className="Info_Accs">
          <div className="Acc_Info">
            <a href="/">Account Information</a>
          </div>
          <div className="Comp_Info">
            <a href="/">Company Information</a>
          </div>
          <div className="Acc_lvl">
            <a href="/">Access Levels</a>
          </div>
        </div>
        <div className="leftbtns_Settings">
          <div>
            <button className="Emailbtn_Settings">
              <i class="far fa-envelope"></i>Email Us
            </button>
            <div className="lftmail_Settings">
              <div>
                <i class="fas fa-envelope"></i> help@
              </div>
              <a href="https://www.smoothhiring.com">smoothhiring.com</a>
            </div>
          </div>
          <div>
            <button className="Supportbtn_Settings">
              <i class="fas fa-phone-alt"></i>Contact Us
            </button>
            <div className="lftphone_Settings">
              <i class="fas fa-phone-alt"></i>1 (877) 789-8767
            </div>
          </div>
        </div>
      </div>

      <div className="NavContainer_Setings">
        <nav className="navbar_Settings">
          <div>
            <ul className="list">
              <li>
                <a href="#">
                  <i class="fas fa-dollar-sign"></i> Pricing
                </a>
              </li>
              <li>
                <a href="#">
                  <i class="fas fa-cog"></i> Settings
                </a>
              </li>
              <li>
                <a href="#">
                  <i class="far fa-question-circle"></i> Help
                </a>
              </li>
              <li>
                <a href="#">
                  <i class="fas fa-sign-out-alt"></i> Log out
                </a>
              </li>
            </ul>
          </div>
        </nav>
        <div className="whole">
          <div className="My_Js_My_As">
            <div className="my_job">
              <h1>My Jobs</h1>
              <span className="arrow"></span>
            </div>
            <div className="my_acc">
              <h1>My Account</h1>
            </div>
          </div>
          <div className="Acc_cntnr">
            <div className="Acnt_Info">
              <h2>Acount Information</h2>
              <div className="form">
                <div>
                  <label for="FName"> First Name</label>
                  <input
                    type="text"
                    name="FName"
                    id="FName"
                    placeholder="Enter your first name"
                    required
                  />
                </div>
                <div>
                  <label for="LName"> Last Name</label>
                  <input
                    type="text"
                    name="LName"
                    id="LName"
                    placeholder="Enter your last name"
                    required
                  />
                </div>
                <div>
                  <label for="email"> Email Address</label>
                  <input
                    type="email"
                    name="email"
                    id="email"
                    placeholder="Enter your email address"
                    required
                  />
                </div>
                <div>
                  <label for="Clndly_token"> Calendly Token</label>
                  <input
                    type="text"
                    name="Clndly_token"
                    id="Clndly_token"
                    required
                  />
                </div>
              </div>
              <div className="Chck_bx">
                <input type="checkbox" id="Check" />
                <label for="Check">New Applicant Notifications</label>
              </div>
              <div className="Updt_btn">
                <button>UPDATE MY ACCOUNT</button>
              </div>
            </div>
            <div className="Pswrd_Sec">
              <h2>Password</h2>
              <div className="Pswd_form">
                <div>
                  <label for="Crnt_pswd"> Current Password</label>
                  <input
                    type="password"
                    name="Crnt_pswd"
                    id="Crnt_pswd"
                    required
                  />
                </div>

                <div>
                  <label for="New_pswd"> New Password</label>
                  <input
                    type="password"
                    name="New_pswd"
                    id="New_pswd"
                    required
                  />
                </div>
              </div>
              <div className="Chng_btn">
                <button>CHANGE PASSWORD</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
export default Account_Info;
